/* 与resume中的section-item通用 */
/* 主要在简历模块的各大区块中的分区信息模块 */
/* 在职位详情页面的职位详情 */
.section-item {
  padding: 30px 0;
  position: relative;

  .section-item-title {
    padding-bottom: 16px;
    @extend .section-item-title-font;
  }

  .block-content {
    @extend .section-item-block-font;

    .block-content-item {
      padding: 4px 0;

      p {
        margin-right: 12px;
      }
    }
  }
  .section-block-title {

    label {
      display: inline-block;
      padding-left: 12px;
      border-left: 3px solid black;
      @extend .section-item-title-font;
    }
  }
}
