body {
  text-align: left;
}

.user-resume {
  display: inline-block;
  width: 720px;
}
.resume-header {
  /* position: fixed;
     z-index: 8; */
  @extend .z-depth-1;
  position: relative;
  width: 720px;
  /* height: 210px; */
  background: $primary-content-color;
}
/* 顶栏个人头像块 */
.user-avatar {
  display: inline-block;
  height: 165px;
  width: 138px;
  position: relative;
  
  img {
    position: absolute;
    /* width: 102px;
       height: 102px; */
    @include size(102px);
    @extend .center;
  }
}
/* 顶栏个人信息块 */
.user-basic-info {
  display: inline-block;
  vertical-align: top;
  min-height: 165px;
  width: 436px;
  padding: 24px 0;
  .personal-info-sns{
    .user-name {
      @extend .user-info-name-font;
      color: $green-2;
      line-height: 32px;
    }
    .resume-sns-edit {
      display: inline-block;
      position: absolute;
      right: 24px;
      line-height: 32px;
      padding-left: 12px;
      .sns {
        vertical-align: middle;
      }
      a {
        display: inline-block;
        font-size: 14px;
        line-height: 32px;
        color: #FFF;
      }
    }
    .hr-action {
      display: inline-block;
      margin-left: 32px;
    }
  }
  .personal-info-multiple {
    display: inline-block;
    vertical-align: top;
    width: 210px;
    .info-item {
      display: inline-block;
      width: 100%;
      position: relative;
      height: 24px;
      line-height: 24px;
      @extend .md-body-14-87;
      @extend text-ellipsis;
      &.multiple-skill {
          height: auto;
      }
      span {
        color: rgba(0,0,0,0.54);
        vertical-align: middle;
      }
      &.languages .skill-content {
        span {
          width: 45px;
          @extend .text-ellipsis;
        }
      }
      &.skills {
        span {
          width: 100%;
          @extend .text-ellipsis;
        }
      }
      label {
        position: absolute;
        padding-left: 25px;
        left: 0;
        width: 160px;
        color: rgba(0,0,0,0.54);
        @extend .text-ellipsis;
        @extend .md-body-14-54;
      }
    }
    .item-title {
      display: inline-block;
      span {
        color: rgba(0,0,0,0.87);
      }
    }
    .skill-content {
      display: inline-block;
      vertical-align: top;
      width: 100px;
      p {
        margin-bottom: 8px;
      }
    }
  }
}

/* 顶栏概览分数块 */
.user-general-score {
  display: inline-block;
  vertical-align: top;
  width: 130px;
  height: 165px;
  .user-score-flex {
    padding-top: 64px;
    font-size: 0;
    height: 100%;
    text-align: right;
    padding-right: 24px;
  }
  .score-item {
    /* 每个各占1/3 */
    /* flex: 1; */
    position: relative;
    display: inline-block;
    width: 33.3%;
    @include size(108px);
    label {
      display: inline-block;
      width: 100%;
      text-align: center;
      color: rgba(0,0,0,0.54);
    }
  }
}
/* 私密信息 */
.private-info-container {
  padding: 0 24px;
}

.private-info {
  background: #FAFAFA;
  @include border-radius(2px);
  line-height: 48px;
  font-size: 14px;
  padding: 0 24px;
  margin-bottom: 12px;
  span {
    padding-right: 32px;
  }
  .private-notify {
    float: right;
    color: $primary-color-green-dark;
  }
}
