.head-menu {
  display: inline-block;
  /* width: 100%; */
  /* 为了确保不会被拉伸  */
  width: 720px;
  height: 42px;
  padding: 0 16px;
  background: #FFF;

  &.fixed {
    position: fixed;
    top: 0;
    z-index: 10;
    @extend .z-depth-1;
  }
  .indicator {
    position: absolute;
    bottom: 0;
    height: 2px;
    background-color: $tabs-underline-color;
    will-change: left, right;
  }
  ul.menu-tabs {
    line-height: 42px;
    /* border-top: 1px dashed rgb(199, 190, 190); */
    border-top: 1px dashed rgb(199, 190, 190);
    font-size: 0;
    li.tab {
      display: inline-block;
      width: 16.66%;
      a {
        display: inline-block;
        width: 100%;
        cursor: pointer;
        text-align: center;
        @extend .info-head-menu-font;
        line-height: 42px;

        &:hover {
          color: $primary-color-green-dark;
        }
      }
      &.active {
        a {
          color: $primary-color-green-dark;
        }
      }
      &.disabled {
        a {
          cursor: default;
          color: rgba(0,0,0,0.28);
          &:hover,
          &.active{
            color: rgba(0,0,0,0.28);
          }
        }
      }
    }
  }
}
