/* 侧边栏的一些样式 */
.resume-sidebar-container {
  display: inline-block;
  vertical-align: top;
  width: 229px;
  margin-left: 7px;
}

.resume-status-container {
  background: #FAFAFA;
  vertical-align: middle;
  text-align: center;

  .resume-uploaded {
    display: none;
    padding: 24px 0;
  }

  .resume-no-upload {
    position: relative;
    color: rgba(0,0,0,0.28);

    .upload-icon {
      font-size: 25px;
    }

    #fileupload {
      cursor: pointer;
      position: absolute;
      z-index: 1;
      top: 0;
      left: 0;
      opacity: 0;
      width: 100%;
      height: 100%;
    }

    #resume-upload-progress {
      position: absolute;
      z-index: 0;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;

      .bar {
        background: #20C05C;
        height: 100%;
        width: 0;
        p {
          word-break: break-word;
          font-size: 14px;
        }
      }
    }
  }

  &.active {

    .resume-uploaded {
      display: inline-block;
    }

    .resume-no-upload {
      display: none;
    }
  }
}

.resume-icon {
  display: inline-block;
  vertical-align: top;
  width: 32px;
  height: 38px;
  position: absolute;
  right: 12px;
}

.resume-status-content {
  display: inline-block;
  text-align: left;
  vertical-align: top;
  height: 38px;

  p {
    color: rgba(0,0,0,0.54);
  }

  b {
    font-size: 16px;
    color: #1481B9;
    word-spacing: 2px;
  }
}

.sidebar-content {
  background: #FFF;
  padding: 16px;

  &.memo {
    width: 100%;
  }
}
/* 初定专属textarea的样式 */
.jx-textarea {
  padding: 6px;
  background: #FAFAFA;
  font-size: 14px;
  color: rgba(0,0,0,0.54);
  resize: none;
  min-height: 60px;
  overflow: hidden;
  display: inline-block;
  @include border-radius(2px);
  outline: none;
  border: none;
}
.footer-action {
  margin-top: 12px;
  display: inline-block;
  text-align: right;

  .footer-item  {
    margin: 0 0 0 4px;
  }
}

/* 人才推荐计划 */
.position-status-container {
  margin-bottom: 12px;
}

.position-push-switch {
  border-top: 1px solid $bg-dark-light;
  line-height: 24px;
  position: relative;

  &.switch-container {
    padding: 12px 0;
  }

  .switch-title {
    color: $font-green-color;
    display: inline-block;
    font-size: 12px;
  }

  .switch {
    display: inline-block;
  }

  .push-question-icon {
    @include size(16px);
    color: #FFF;
    cursor: pointer;
    background: #E6E6E6;
    display: inline-block;
    @include border-radius(8px);
    line-height: 16px;
    position: relative;
    text-align: center;
    vertical-align: middle;


    .push-interpretation {
      background: #8F8F8F;
      color: #FFF;
      display: none;
      font-size: 12px;
      position: absolute;
      left: -100px;
      top: -160px;
      width: 200px;
      z-index: 10;
      padding: 12px;
      text-align: left;
      @include border-radius(2px);

      p {
        line-height: 20px;
      }

      &:before {
        content:"";
        position: absolute;
        top: 100%;
        left: 50%;
        width: 0;
        height: 0;
        border-top: 8px solid #8F8F8F;
        border-right: 8px solid transparent;
        border-left: 8px solid transparent;
      }
    }
    &:hover {
      .push-interpretation {
        display: block;
      }
    }
  }
  $resume-evaluate-status-color: (
    "passed": '#13A277',
    "processing": '#F77A25',
    "initial": '#1481B9'
  );


  .resume-evaluate-status {
    padding: 12px 0 0 0;

    @each $status, $color in $resume-evaluate-status-color {
      &.#{$status} {
        span {
          padding-left: 12px;
          border-left: 4px solid #{$color};
          color: #{$color};
        }
      }
    }

    &.mute {
      display: none;
    }

    .evaluate-action {
      margin-top: 12px;
    }

    button {
      border: none;
      background: #1481B9;
      color: #FFF;
      width: 100%;
      height: 24px;
      @include size(191px, 24px);
    }
  }
}

.record-container {

  .record-item {
    width: 100%;

    p {
      line-height: 20px;
    }
    b {
      width: 65px;
      @extend .text-ellipsis;
    }
    time {
      float: right;
    }
  }
}

.talent-item {
  .talent-avatar {
    width: 30%;
    max-width: 48px;
    max-height: 48px;
    display: inline-block;

    img {
      max-width: 100%;
      max-height: 100%;
    }
  }
  .talent-info {
    display: inline-block;
    width: 70%;
  }
  .talent-name {
    display: inline-block;
    width: 100%;
    font-size: 14px;
    font-weight: bold;
    color: $primary-color;
  }
  .talent-content {
    height: 20px;
    .content-item {
      display: inline-block;
      padding-right: 8px;
      color: rgba(0,0,0,0.54);
    }
  }
}

/* 侧边栏 */
.resume-container{
  position: relative;
  background: $uploader-background-color;
  height: $uploader-height;
  width: 100%;

  &:hover {
    background: $uploader-background-color;
    height: $uploader-height;
  }


  #resume-picker {
    position: absolute;
    z-index: 5;
    height: 100%;
    width: 100%;
    .progress-bar {
      position: absolute;
      left: 0;
      top: 0;
      height: $uploader-height;
    }


    label{
      height: $uploader-height;
      width: 0;
      background: $uploader-label-color;
    }
  }
}

#resume-uploader{
  position: absolute;
  top: 0;
  display: inline-block;
  padding: 0;
  height: $uploader-height;
  text-align: center;
  z-index: 3;
  span{
    display: block;
    margin: 0 auto;
    position: relative;
    top: 20px;
    color: black;
  }
  .progress-bar{
    height: 100%;
  }
  .resume-actions{
    position: absolute;
    top: 0;
    width: 100%;
    line-height: $uploader-height;
    .resume-icon{
      margin-top: 12px;
    }
    p{
      margin: 0;
      line-height: 20px;
      text-align: center;
    }
  }
}

.resume-patch-release {
  .new-item {
    display: none;
  }
  .action-content {
    cursor: pointer;
  }
  .sidebar-content.patch-item {
    .footer-action {
      display: none;
    }
  }
  &.active {
    .action-content {
      display: none;
    }
    .new-item {
      display: block;
    }
  }
  .patch-lists {
    .jx-textarea {
      padding: 6px;
    }
  }
}

.talent-fillin {
  @extend .z-depth-1;
  margin-bottom: 16px;
}

/* 公告栏 */
.announcement {
  .sidebar-content {
    color: rgba(0, 0, 0, 0.54);
  }
}

.share-kindle {
  @extend .z-depth-1;
  background: #fff;
  color: rgba(0,0,0,0.54);
  display: inline-block;
  padding: 12px;
  padding-bottom: 0;
  position: relative;
  margin-bottom: 12px;
  vertical-align: top;
  width: 230px;
  height: 67px;

  .share-caption {
    color: rgba(0,0,0,0.87);
    font-size: 14px;
    float: left;

    p {
      font-size: 12px;
      margin-top: 12px;
    }
  }

  .sidebar-media {
    float: right;
    right: 24px;
  }
}
