/* 最近关注 */
.resume-skill-focus {
  .skill-fields {
    padding-top: 12px;
  }
  
  .field-item {
    display: table-cell;
    width: 1%;
    vertical-align: top;
    margin-right: 24px;
    &:hover {
      background: #FAFAFA;
    }

    .field-item-pic {
      position: relative;
      height: 300px;
      width: 100%;
      .item-pic-container {
        @extend .center;
        height: 150px;
        width: 150px;
      }
    }
  }
    /* 角标 */
  .mark-item {
    font-size: 0;
    margin-bottom: 8px;
  }

  .corner-mark-list {
    padding-left: 32px;
  }

  .corner-mark {
    .corner-icon {
      display: inline-block;
      @include size(12px);
    }
    .mark-content {
      line-height: 12px;
      font-size: 12px;
      padding-left: 8px;
      color: rgba(0,0,0,0.54);
    }
  }

  .score-list {
    .score-item {
      font-size: 0;
      margin-bottom: 8px;
    }

    .score-item-title {
      display: inline-block;
      font-size: 14px;
      width: 50%;
      color: rgba(0,0,0,0.54);
      word-break: break-word;
    }
    .score-bar-container {
      display: inline-block;
      vertical-align: top;
      width: 45%;
      margin-top: 5px;
    }
  }
}

