.primary-bottom-border {
  border-bottom: 2px solid $primary-color;
}

.sidebar-card {
  width: $sidebar-default-width;
  display: inline-block;
}
.sidebar {
  display: inline-block;
  margin-bottom: 12px;
  vertical-align: top;
  width: $sidebar-default-width;
  /* 由于margin也是属于盒模型的一部分，所以当设置margin的时候，阴影部分也会显示 */
  /* 外套一层可以解决 */
  .sidebar-container {
    @extend .z-depth-1;
    background: $sidebar-content-bg-color;

    /* 另一种样式，在个人简历页面出现 */
    &.fullbar {

      .sidebar-title {
        background: $primary-color;

        span {
          border-left: 4px solid #FFF;
          color: #FFF;
        }
      }

    }
  }

  .sidebar-title {
    position: relative;
    line-height: 32px;
    padding: 0 16px;
    /* @extend .primary-bottom-border; */

    span {
      border-left: 4px solid $font-green-color;
      color: rgba(0,0,0,0.87);
      font-size: 14px;
      padding-left: 6px;

    }

    .title-action {
      float: right;
      right: 24px;
      margin-top: 8px;
      cursor: pointer;
    }
  }

  .sidebar-item {
    display: inline-block;
    width: 100%;
  }

}
.sidebar-content {

  .text-display-item {
    margin-bottom: 12px;
    padding: 16px;
  }

  .info-item {
    @extend .md-body-14-87;
    line-height: 24px;

    i {
      padding: 0 8px;
    }
  }



}
.sidebar-action {
  background: #FFF;
  display: inline-block;
  width: $sidebar-default-width;
  @extend .z-depth-1;
  border-left: 2px solid $primary-color;

  .action-title{
    font-size: 14px;
    font-weight: bold;
    color: rgba(0,0,0,0.87);
    padding: 8px 8px 8px 16px;
  }
  .action-content {
    font-size: 12px;
    padding: 8px 8px 8px 16px;
    color: rgba(0,0,0,0.54);
  }
}
/* sidebar与顶栏同一层级的action样式 */
.sidebar-extra {
  display: inline-block;
  float: right;
  color: $primary-color;
  cursor: pointer;
}
/* 首先是简历页面的私人备注 */
.footer-action {
  width: 100%;
}
