/* Remove Focus Boxes */
select:focus {
  outline: 1px solid lighten($secondary-color, 47%);
}
button:focus {
  outline: none;
}

label {
  font-size: $label-font-size;
  color: $input-border-color;
}

/***************************
   Text Inputs + Textarea
 ****************************/

// Style Placeholders
::-webkit-input-placeholder {
  color: $input-placeholder-color;
}

:-moz-placeholder { /* Firefox 18- */
  color: $input-placeholder-color;
}

::-moz-placeholder {  /* Firefox 19+ */
  color: $input-placeholder-color;
}

:-ms-input-placeholder {
  color: $input-placeholder-color;
}
input[placeholder] {
    color: rgba(0,0,0,0.28);
}

// Text inputs
input[type=text],
input[type=password],
input[type=email],
input[type=url],
input[type=time],
input[type=date],
input[type=datetime-local],
input[type=tel],
input[type=number],
input[type=search],
textarea.materialize-textarea {

  // General Styles
    background-color: #FFF;
  border: 1px solid $input-border-color;
  padding-left: 8px;
  @include border-radius(2px);
  outline: none;
  height: 36px;
  width: 100%;
  @extend .input-field-font;
  margin-bottom: 8px;
  box-shadow: none;
  @include box-sizing(border-box);
  transition: all .3s;

  // Disabled input style
  &:disabled, &[readonly="readonly"] {
    color: $input-disabled-color;
    border: 1px dotted $input-disabled-color;
  }
  // Disabled label style
  &:disabled+label, &[readonly="readonly"]+label {
    color: $input-disabled-color;
  }
  // Focused input style
  &:focus:not([readonly]) {
    border: 1px solid $input-focus-color;
  }
  // Focused label style
  &:focus:not([readonly])+label {
    color: $input-focus-color;
  }
  // Valid Input Style
  &.valid,
  &:focus.valid {
    border: 1px solid $input-success-color;
  }
  // Custom Success Message
  &.valid + label:after,
  &:focus.valid + label:after {
    content: attr(data-success);
    color: $input-success-color;
    opacity: 1;
  }
  // Invalid Input Style
  &.invalid,
  &:focus.invalid {
    border: 1px solid $input-error-color;
  }
  // Custom Error message
  &.invalid + label:after,
  &:focus.invalid + label:after {
    content: attr(data-error);
    color: $input-error-color;
    opacity: 1;
  }
}

// Styling for input field wrapper
.input-field {
  position: relative;
  /* height: 60px; */
  margin-top: 20px;

  input:after{
    display: block;
    content: attr(data-error);
    position: absolute;
    top: 55px;
    opacity: 1;
    /* transition: .2s opacity ease-out, .2s color ease-out; */

  }
  label {
    position: absolute;
    top: -20px;
    left: 0;
    font-size: 12px;
    color: rgba(0,0,0,0.54);
    /* @extend .input-label-font; */
    cursor: text;
    /* @include transition(.2s ease-out); */
  }
  label.active {
    /* @include transform(translateY(-140%)); */
  }

  .help-block{
    display: inline-block;
    height: 17px;
    width: 100%;
    @extend .input-field-error-font;
  }

  // Prefix Icons
  .prefix {
    position: absolute;
    width: 3rem;
    font-size: 2rem;
    @include transition(color .2s);

    &.active { color: $input-focus-color; }
  }
  .prefix ~ input,
  .prefix ~ textarea {
    margin-left: 3rem;
    width: 92%;
    width: calc(100% - 3rem);
  }
  .prefix ~ textarea { padding-top: .8rem; }
  .prefix ~ label { margin-left: 3rem; }

  @media #{$medium-and-down} {
    .prefix ~ input {
      width: 86%;
      width: calc(100% - 3rem);
    }
  }
  @media #{$small-and-down} {
    .prefix ~ input {
      width: 80%;
      width: calc(100% - 3rem);
    }
  }
}

// Search Field

.input-field input[type=search] {
  display: block;
  line-height: inherit;
  padding-left: 4rem;
  width: calc(100% - 4rem);

  &:focus {
    background-color: #FFF;
    border: 0;
    box-shadow: none;
    color: #444;

    & + label i,
    & ~ .mdi-navigation-close {
      color: #444;
    }
  }

  & + label {
    left: 1rem;
  }

  & ~ .mdi-navigation-close {
    position: absolute;
    top: 0;
    right: 1rem;
    color: transparent;
    cursor: pointer;
    font-size: 2rem;
    transition: .3s color;
  }
}


// Default textarea
textarea {
  width: 100%;
  height: 20px;
  padding: 8px 0;
  background-color: transparent;

  &.materialize-textarea {
    overflow-y: hidden; /* prevents scroll bar flash */
    height: 20px;
    padding: 8px 0;
    resize: none;
    min-height: 20px;
  }
}


// For textarea autoresize
.hiddendiv {
  display: none;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */
  padding-top: 1.2rem; /* prevents text jump on Enter keypress */
}


/***************
   Radio Buttons
 ***************/

/* Remove default Radio Buttons */
[type="radio"]:not(:checked),
[type="radio"]:checked {
  position: absolute;
  left: -9999px;
  visibility: hidden;
}

[type="radio"]:not(:checked) + label,
[type="radio"]:checked + label {
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  display: inline-block;
  height: 25px;
  line-height: 25px;
  font-size: 1rem;
  @include transition(.28s ease);

  -webkit-user-select: none; /* webkit (safari, chrome) browsers */
  -moz-user-select: none; /* mozilla browsers */
  -khtml-user-select: none; /* webkit (konqueror) browsers */
  -ms-user-select: none; /* IE10+ */
}

[type="radio"] + label:before,
[type="radio"] + label:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  margin: 4px;
  width: 16px;
  height: 16px;
  z-index: 0;
  @include transition(.28s ease);
}


/* Unchecked styles */
[type="radio"]:not(:checked) + label:before {
  border-radius: 50%;
  border-bottom: 2px solid $radio-empty-color;
}
[type="radio"]:not(:checked) + label:after {
  border-radius: 50%;
  border-bottom: 2px solid $radio-empty-color;
  z-index: -1;

  @include transform(scale(0));
}

/* Checked styles */
[type="radio"]:checked + label:before {
  border-radius: 50%;
  border-bottom: 2px solid transparent;
}
[type="radio"]:checked + label:after {
  border-radius: 50%;
  border-bottom: 2px solid $radio-fill-color;
  background-color: $radio-fill-color;
  z-index: 0;
  @include transform(scale(1.02));
}

/* Radio With gap */
[type="radio"].with-gap:checked + label:before {
  border-radius: 50%;
  border-bottom: 2px solid $radio-fill-color;
}
[type="radio"].with-gap:checked + label:after {
  border-radius: 50%;
  border: 2px solid $radio-fill-color;
  background-color: $radio-fill-color;
  z-index: 0;
  @include transform(scale(.5));
}

/* Disabled Radio With gap */
[type="radio"].with-gap:disabled:checked + label:before {
  border: 2px solid $input-disabled-color;
}
[type="radio"].with-gap:disabled:checked + label:after {
  border: none;
  background-color: $input-disabled-color;
}

/* Disabled style */
[type="radio"]:disabled:not(:checked) + label:before,
[type="radio"]:disabled:checked + label:before {
  background-color: transparent;
  border-color: $input-disabled-color;
}
[type="radio"]:disabled + label {
  color: $input-disabled-color;
}
[type="radio"]:disabled:not(:checked) + label:before {
  border-color: $input-disabled-color;
}
[type="radio"]:disabled:checked + label:after {
  background-color: $input-disabled-color;
  border-color: $input-disabled-solid-color;
}


/***************
   Checkboxes
 ***************/

/* CUSTOM CSS CHECKBOXES */
form p {
  margin-bottom: 10px;
  text-align: left;
}
form p:last-child {
  margin-bottom: 0;
}

/* Remove default checkbox */
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
  visibility: hidden;
}


// Checkbox Styles
[type="checkbox"] {

  // Text Label Style
  + label {
    position: relative;
    padding-left: 24px;
    cursor: pointer;
    display: inline-block;
    height: 25px;
    line-height: 25px;
    font-size: 1rem;

    -webkit-user-select: none; /* webkit (safari, chrome) browsers */
    -moz-user-select: none; /* mozilla browsers */
    -khtml-user-select: none; /* webkit (konqueror) browsers */
    -ms-user-select: none; /* IE10+ */
  }

  /* checkbox aspect */
  + label:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 18px;
    height: 18px;
    z-index: 0;
    border: 2px solid $radio-empty-color;
    border-radius: 1px;
    margin-top: 2px;
    @include transition(.2s);
  }

  &:not(:checked):disabled + label:before {
    border: none;
    background-color: $input-disabled-color;
  }

}

[type="checkbox"]:checked {
  + label:before {
    top: -1px;
    left: -3px;
    width: 5px;
    height: 8px;
    border-top: 2px solid transparent;
    border-left: 2px solid transparent;
    border-right: 2px solid $radio-fill-color;
    border-bottom: 2px solid $radio-fill-color;
    @include transform(rotate(40deg));
    -webkit-backface-visibility: hidden;
    @include transform-origin(100% 100%);
  }

  &:disabled + label:before {
    border-right: 2px solid $input-disabled-color;
    border-bottom: 2px solid $input-disabled-color;
  }

}

/* Indeterminate checkbox */
[type="checkbox"]:indeterminate {
  +label:before {
    left: -10px;
    top: -11px;
    width: 10px; height: 22px;
    border-top: none;
    border-left: none;
    border-right: 2px solid $radio-fill-color;
    border-bottom: none;
    @include transform(rotate(90deg));
    -webkit-backface-visibility: hidden;
    @include transform-origin(100% 100%);
  }

  // Disabled indeterminate
  &:disabled + label:before {
    border-right: 2px solid $input-disabled-color;
    background-color: transparent;
  }
}


// Filled in Style
[type="checkbox"].filled-in {
  // General
  + label:after {
    border-radius: $checkbox-border-radius;
  }
  + label:before,
  + label:after {
    content: '';
    left: 0;
    position: absolute;
    top: 5px;
    /* .1s delay is for check animation */
    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;
    z-index: 1;
  }
  // Unchecked style
  &:not(:checked) + label:before {
    width: 0;
     height: 0;
     border: 3px solid transparent;
     left: 6px;
     top: 10px;

     -webkit-transform: rotateZ(37deg);
             transform: rotateZ(37deg);
     -webkit-transform-origin: 20% 40%;
             transform-origin: 100% 100%;
  }
  &:not(:checked) + label:after {
    height: $checkbox-size;
    width: $checkbox-size;
    background-color: transparent;
    border: 1px solid $checkbox-border-color;
    top: 5px;
    z-index: 0;
  }
  // Checked style
  &:checked {
    + label:before {
      top: 4px;
      left: 1px;
      width: 5px;
      height: 8px;
      border-top: 1px solid transparent;
      border-left: 1px solid transparent;
      border-right: 1px solid $input-bg-color;
      border-bottom: 1px solid $input-bg-color;
      -webkit-transform: rotateZ(30deg);
      transform: rotateZ(30deg);

      -webkit-transform-origin: 100% 100%;
      transform-origin: 100% 100%;
    }
    + label:after {
      top: 5px;
      height: $checkbox-size;
      width: $checkbox-size;
      border: 1px solid $secondary-color;
      background-color: $secondary-color;
      z-index: 0;
    }
  }
  // Disabled style
  &:disabled:not(:checked) + label:before {

    background-color: transparent;
    border: 2px solid transparent;
  }
  &:disabled:not(:checked) + label:after {
    border-color: transparent;
    background-color: $input-disabled-solid-color;
  }
  &:disabled:checked + label:before {
    background-color: transparent;

  }
  &:disabled:checked + label:after {
    background-color: $input-disabled-solid-color;
    border-color: $input-disabled-solid-color;
  }

}

/***************
   Switch
 ***************/
.switch,
.switch * {
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
.switch label {
  cursor: pointer;
}
.switch label input[type=checkbox]{
  opacity: 0;
  width: 0;
  height: 0;
}
.switch label input[type=checkbox]:checked + .lever {
  background-color: $switch-checked-lever-bg;
}
.switch label input[type=checkbox]:checked + .lever:after {
  background-color: $switch-bg-color;
}
.switch label .lever {
  content: "";
  display: inline-block;
  position: relative;
  width: 40px;
  height: 15px;
  background-color: $switch-unchecked-lever-bg;
  border-radius: 15px;
  margin-right: 10px;
  transition: background 0.3s ease;
  vertical-align: middle;
  margin: 0 16px;
  overflow: visible;
}
.switch label .lever:after {
  content: "";
  position: absolute;
  display: inline-block;
  width: 21px;
  height: 21px;
  background-color: $switch-unchecked-bg;
  border-radius: 21px;
  box-shadow: 0 1px 3px 1px rgba(0,0,0,0.4);
  left: -5px;
  top: -3px;
  transition: left 0.3s ease, background 0.3s ease, box-shadow 0.1s ease;
}
// Switch active style
input[type=checkbox]:checked:not(:disabled) ~ .lever:active:after {
  box-shadow: 0 1px 3px 1px rgba(0,0,0,0.4), 0 0 0 15px transparentize($switch-bg-color, .9);
}
input[type=checkbox]:not(:disabled) ~ .lever:active:after {
  box-shadow: 0 1px 3px 1px rgba(0,0,0,0.4), 0 0 0 15px rgba(0, 0, 0, 0.08);
}
.switch label input[type=checkbox]:checked + .lever:after {
  left: 24px;
}

// Disabled Styles

.switch input[type=checkbox][disabled] + .lever{
  cursor: default;
}
.switch label input[type=checkbox][disabled] + .lever:after,
.switch label input[type=checkbox][disabled]:checked + .lever:after {
  background-color: $input-disabled-solid-color;
}




/***************
   Select Field
 ***************/

.select-label {
  position: absolute;
}
.arrow-container {
    display: none;
}
.select-wrapper {
    position: relative;

    input.select-dropdown {
        position: relative;
        display: block;
        margin-bottom: 8px;
        border: none;
        border-bottom: 1px solid $input-border-color;
        height: 35px;
        width: 100%;
        cursor: pointer;
        background-color: transparent;
        @extend .input-field-font;
        outline: none;
    }
    input.select-dropdown {
        background: #FFF;
        box-sizing: border-box;
        border: 1px solid #E6E6E6;
        border-radius: 2px;
        padding: 0 8px 0 12px;
    }
    .arrow-container {
        display: inline-block;
        position: absolute;
        width: 21px;
        height: 100%;
        right: 0;
        border: 1px solid #E6E6E6;
        z-index: 1;
        i {
            top: 16px;
            right: 6px;
            z-index: 1;
        }
    }
    & + label {
        position: absolute;
        top: -20px;
    }

}
.mdi-navigation-arrow-drop-down {
    color: initial;
    position: absolute;
    right: 0;
    top: 0;
    font-size: 23px;
    &.disabled {
        color: $input-disabled-color;
    }
}

/* 自定义 */
.input-field {
    .select-wrapper {
        /* margin-top: 8px; */
        input.select-dropdown {
            display: inline-block;
            background: #FFF;
            box-sizing: border-box;
            border: 1px solid #FFF;
            border-radius: 2px;
            padding: 0 8px 0 12px;
            border: 1px solid #E6E6E6;
            @include border-radius(2px);
            outline: none;
            height: 35px;
