@import 'jx-side-menu';
@import 'jx-scrollbar';

body {
  height: 100%;
  min-height: 620px;
  text-align: center;
  /* overflow: hidden; */
}

/* 整个卡片的外包 */
.section-container {
  position: relative;
  @extend .z-depth-1;
  /* background : $primary-content-color; */
  height: 100%;
  min-height: $side-card-min-height;
  /* display: flex; */
  font-size: 0;
  text-align: left;
}

/* 卡片的右侧 */
.section-content {
  /* position: absolute;
     top: 0;
     left: $side-menu-width; */
  background: $primary-content-color;
  display: inline-block;
  vertical-align: top;
  width: 780px;
  height: 100%;
  text-align: left;
}

.side-card {
  .section-content-header {
    .section-title {
      font-weight: bold;
    }
  }
}
/* 主要内容右侧的header */
/* TODO:期望能滑动的时候固定在顶部 */
.section-content-header {
  background: $primary-bg-color;
  @extend .bottom-border;
  /* padding: 0 $jx-content-side-padding 0 $side-menu-width + $jx-content-side-padding; */
  padding: 0 $jx-content-side-padding ;
  /* position: absolute; */
  z-index: 3;
  /* width: 100%; */
  left: 0;
  line-height: $section-header-height;
  height: $section-header-height;

  .section-title {
    @extend .common-title-font;
    display: inline-block;
    vertical-align: top;
    padding-right:24px;

    /* width: 150px; */
    height: $section-header-height;
    line-height: $section-header-height;

    a {
      display: inline-block;
      line-height: $section-header-height;
      cursor: pointer;
      @extend .common-title-font;
    }
  }

  .header-content {
    display: inline-block;
    width: 380px;
    height: $section-content-header-height;
    line-height: $section-content-header-height;

    a {
      vertical-align: middle;
    }
  }

  .header-action {

    position: absolute;
    top: 0;
    right: $jx-content-side-padding;
    display: inline-block;
    width: 240px;
    text-align: right;


    a {
      padding: 0 4px;
      display: inline-block;
      line-height: $section-header-height;
      @extend .extra-action-font;

      &.disable {
        color: rgba(0,0,0,0.28);
        cursor: not-allowed;
      }

      &.page-action {
        line-height: 24px;
      }
    }
  }
}
/* 右侧的主要内容 */
.section-content-main {
  /* margin-top: $section-content-header-height; */
  /* height: $side-card-min-height - $section-content-header-height; */
  min-height: 500px;
  overflow-y: auto;
  @extend .position-info-font;

}
/* 一般出现textarea的样式 */
.input-container {
  padding-bottom: 16px;
  textarea {
    background-color: #FAFAFA;
    border: none;
    border-radius: 0;
    outline: none;
    height: 38px;
    width: 100%;
    resize: none;
    font-size: 14px;
    letter-spacing: 1px;
    text-indent: 20px;
    margin: 0 0 24px 0;
    padding: 8px;
    box-shadow: none;

  }
}
.invtation-details-container {
  padding: 24px 16px;
}
