.section-content {
  width: 100%;
  left: 0;
  z-index: 1;
  padding-left: $side-menu-width;

}





/* 语言技能模块 */
.repository-lang-stats {
  position: relative;
  background: #FAFAFA;

  .repository-lang-stats-numbers {

    li {
      display: table-cell;
      width: 1%;
      padding: 11px 5px;
      text-align: center;
      white-space: nowrap;
      border-bottom: 0;

      a {
        font-weight: bold;
        color: #999;
        text-decoration: none;
      }
    }

    .language-color {
      display: inline-block;
      width: 10px;
      height: 10px;
      border-radius: 50%;
    }
  }
}

.repository-lang-stats-graph {
  display: table;
  width: 100%;
  overflow: hidden;
  height: 8px;
  white-space: nowrap;
  border: 1px solid #ddd;
  border-top: 0;
  border-radius: 4px;

  .language-color {
    display: table-cell;
    line-height: 8px;
    text-indent: -9999px;
  }
}

/* 中间主要部分 */
.skill-main {
  width: 100%;
  margin-top: 16px;
  box-shadow: none;
}

.skill-description {
  width: auto;

  .skill-language-description {
    padding: 12px 24px;;
  }
  .description-item {
    .icon {
      vertical-align: top;
    }
    .item-content {
      display: inline-block;
      width: 410px;
    }
    p {
      width: 430px;
      /* 和icon的高度保持一致 */
      line-height: 28px;
      color: rgba(0,0,0,0.54);
    }
  }
}


/* 技能标签 */
.skill-tag-footer {
  padding: 12px 0;
}
.skill-list-header {
  font-size: 14px;
  line-height: 36px;
  background: #FAFAFA;
  padding: 0 12px;
}
.tag-list {
  padding: 12px 0;
}
.tag-item {
  display: inline-block;
  margin-bottom: 8px;
  .skill-pill {
    display: inline-block;
    font-size: 0;
  }
}
.endorse-count {
  display: inline-block;
  vertical-align: top;
  width: 36px;
  padding: 0 2px;
  line-height: 24px;
  font-weight: bold;
  font-size: 12px;
  -webkit-transition: min-width 0.2s linear;
  -moz-transition: min-width 0.2s linear;
  -o-transition: min-width 0.2s linear;
  transition: min-width 0.2s linear;
  background-color:  #C1EDD2;
  -webkit-border-radius: 3px 0 0 3px;
  -moz-border-radius: 3px 0 0 3px;
  -ms-border-radius: 3px 0 0 3px;
  -o-border-radius: 3px 0 0 3px;
  border-radius: 3px 0 0 3px;
  text-align: center;

  .num-endorsements {
    font-weight: bold;
    color: #37AE86;
    display: inline;
  }
}
.endorse-item-name {
  display: inline-block;
  padding: 0 13px;
  height: 24px;
  line-height: 24px;
  background-color: #F6F7F9;
  -webkit-border-radius: 0 3px 3px 0;
  -moz-border-radius: 0 3px 3px 0;
  -ms-border-radius: 0 3px 3px 0;
  -o-border-radius: 0 3px 3px 0;
  border-radius: 0 3px 3px 0;
}
.endorse-item-name-text {
  font-size: 14px;
  color: rgba(0,0,0,0.54);
  text-decoration: none;
}

/* sidebar score display */
.main-side-menu {
  z-index: 2;
}

.score-display-menu {

  a.score-title {
    display: inline-block;
    width: 90px;
    vertical-align: top;
  }
  .score-bar-container {
    display: inline-block;
    margin-left: 12px;
    width: 70px;
  }

}

.score-bar {
  background: #FFF;
  height: 8px;
  border-radius: 4px;
  border: 1px solid #d9d9d9;

  .percent {
    background: #FFF;
    height: 7px;
    border-radius: 4px;
    -webkit-transition: background, 0.3s, ease;
  }
}
