.detail-container {
  /* margin-top: 290px; */
  width: 720px;
}

/* 测评详情section */
.resume-section {
  width: 100%;
  margin-top: 16px;
  background: $primary-content-color;
  @extend .z-depth-1;

  .resume-section-title {
    display: inline-block;
    width: 100%;

    .title-container {
      height: 30px;
      width: 138px;
      padding: 0 16px;
    }

    .title-indicator {
      display: inline-block;
      width: 100%;
      height: 12px;
      background: #5BA58A;
    }
    .title-content {
      text-align: center;
      label {
        @extend .resume-section-title-font;
      }
    }
  }
  .section-item-block {
    width: 475px;
  }

  .section-graph.evaluate-graph {
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
  }
}

.resume-section-content {
  padding: 0 16px;
  /* 现在由于是自己写的有序列表 */
  /* 之后导入数据应该不用了 */
  min-height: 240px;

  .section-item .block-content {

    padding: 12px 0 12px 40px;
  }
}
.resume-evaluate {

  .skill-score.section-item {
    border-bottom: 1px dashed rgb(199, 190, 190);
  }
}

.resume-basic-details {

  .section-item {
    padding: 0;

    &:first-child {
      padding: 30px 0 0 0;
    }

    &.extra-detail-info {
      .block-content {
        padding-left: 21px;
      }
    }
  }
  .section-item-title {
    height: 32px;
    line-height: 32px;
    background: #FAFAFA;
    padding: 0;
    padding-left: 12px;
  }
  .section-item-block {
    padding-top: 24px;
    border-bottom: 1px solid #E6E6E6;
    width: 100%;

    label {
      border: none;
      font-weight: bold;
    }

    .block-content {
      padding: 0 0 12px 13px;

      .jx {
        background: #FFF;
        line-height: 20px;
      }

      .block-content-item {

        padding-right: 16px;
        line-height: 32px;
      }
    }
    &:last-child {
      border: none;
    }
  }
}

.radar-chart {
  position: absolute;
  right: 0;
  top: 12px;
}
