.main-side-menu {
  /* @extend .z-depth-1; */
  /* position: absolute; */
  /* z-index: 10; */
  /* top: 0;
     bottom: 56px; */
  width: $side-menu-width;
  height: 100%;
  background: $primary-bg-color;
  display: inline-block;
  min-height: 500px;
  float: left;
  vertical-align: top;

  .tab {
    cursor: pointer;
    box-sizing: border-box;
    position: relative;
    width: 100%;
    height: 48px;
    line-height: 48px;
    background: $side-tab-bg-color;

    @extend .position-content-font;

    &:hover, &.active {
      background: $side-tab-hover-color;
      border-left: 2px solid $side-tab-border-color;
      color: rgba(0,0,0,0.87);
    }
    a {
      display: inline-block;
      width: 100%;
      height: 100%;
      padding-left: 16px;
      color: inherit;
    }
    span {
      position: absolute;
      top: 0;
      right: 16px;
    }
  }
}
