/* 账号信息 */
.Account-github {
  overflow-y: visible;

}
.account-info-main,
.skill-main{

  .section-content {
    display: none;
    &.active {
      display: block;
    }
  }
  .tab {
    .account-type {
      padding-left: 8px;
    }
  }
}
.section-content-main {

  .type-title {
    line-height: 32px;
    font-size: 12px;
    font-weight: bold;
    background: #FAFAFA;
    padding-left: 12px;
  }
}

.Account-github-repos {


  .repos-item {
    height: 72px;
    background: #FFF;
    font-size: 0;
    &:hover {
      background: #E6E6E6;
    }
    .repos-basic-info {
      display: inline-block;
      vertical-align: top;
      width: 65%;
      height: 100%;
      font-size: 14px;
      padding: 12px;
      .repos-name {
        margin-bottom: 12px;
      }
    }

    .repos-statistics {
      display: inline-block;
      width: 35%;
      font-size: 14px;
      padding: 12px;

      .statistics-most {
        display: inline-block;
        text-align: right;
        width: 100%;
        font-weight: bold;
        margin-bottom: 12px;
      }
      .statistics-container {
        text-align: right;
      }

      .stat-item {
        display: inline;
        padding: 0 4px;
      }
    }
  }
}
/* StackOverFlow部分 */
.post-container {
  position: relative;
  padding: 0 12px;
}
.sof-skill-tag {
  padding: 8px 12px;

  .tag-item {
    padding: 0 6px;
    font-weight: bold;
    a {
      color: #1481B9;
      -webkit-transition: color 0.15s ease, background 0.15s ease, border 0.15s ease;
      -moz-transition: color 0.15s ease, background 0.15s ease, border 0.15s ease;
      -ms-transition: color 0.15s ease, background 0.15s ease, border 0.15s ease;
      -o-transition: color 0.15s ease, background 0.15s ease, border 0.15s ease;
    }

    $levels: (
      'level-1': 16,
      'level-2': 14,
      'level-3': 12,
      'level-4': 10
    );
    @each $level-class, $font-size in $levels {
      &.#{$level-class} {
        a {
          font-size: $font-size + px;
        }
      }
    }
    &.level-1 {

      a {
        line-height: 30px;
        background: #DFE8F1;
        border: 1px solid #e4edf4;
        padding: 0 4px;
        display: inline-block;
        border-radius: 2px;
      }
    }

  }
}
[class^="icon-sof"]{
  background-image: url(http://cdn.sstatic.net/img/user-profile-sprite.svg?v=7ed7725bbe60), none;
  width: 19px;
  height: 22px;
  display: inline-block;
  vertical-align: middle;
}
.icon-sof-a {
  background-position: 1px -28px;

  &.accepted {
    background-position: -49px -28px, -49px -28px;
  }
}

.icon-sof-q {
  background-position: 1px 0;
  &.accepted {
    background-position: -49px 0;
  }
}
.vote {
  display: inline-block;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.3;
  text-align: center;
  color: #777;
  min-width: 36px;
  height: auto;
  padding: 3px 6px;
  margin: 0 8px;
  border: 1px solid #b9b9b9;
  border-radius: 2px;

  &.accepted {
    border-color: #75845c;
    background: #75845c;
    color: #e1e818;
  }
}
.sof-item {
  line-height: 32px;
}
.question-hyperlink,
.answer-hyperlink{
  color: #07c;
}

/* 技术活跃时间按钮 */
.resume-section.resume-active-time{
  height: 500px;
  .resume-section-title {
    .title-container {
      width: 100%;
      .title-indicator {
        width: 106px;
      }
      .title-content {
        width: 106px;
      }
      .trigger-container {
        vertical-align: top;
        display: inline-block;
        float: right;
      }
    }
  }
  .resume-section-content {
    display: inline-block;
  }
  .time-week,
  .time-day{
    background: #FFF;
    position: absolute;
    z-index: 1;
    &.active {
      z-index: 2;
    }
  }
}
